.About {
  display: flex;
  justify-content: center;
  margin-top: 2em;
}

.Text {
  width: 18em;
  align-self: center;
  display: flex;
  flex-direction: column;
  align-content: flex-end;
}

.Profession {
  color: var(--header-color);
  font-family: var(--ff-accent);
  font-size: clamp(calc(0.2vw + 0.8em), 3.2vw, 1.3em);
  text-align: right;
  margin-bottom: 0.5em;
  font-weight: 400;
}

/* @keyframes fadeProfession {
  100% {
    opacity: 1;
    transform: translateY(0);
  }
} */

@media screen and (max-width: 480px) {
  .About {
    margin-top: 0;
    display: grid;
    justify-content: center;
    justify-items: center;
  }

  .Text {
    width: 10em;
  }

  .Text,
  .Profession {
    margin-left: 0;
  }
}

.Stack {
  font-size: clamp(0.6em, 0.4vw, 1.2em);
  text-align: right;
  margin-bottom: 2em;
  letter-spacing: 0.19rem;
}


@media screen and (max-device-width: 857px) {
  .About {
    margin-top: 1em;
    flex-wrap: wrap;
  }

  .Text .Stack {
    text-align: center;
  }

  .Text .Profession {
    text-align: center;
  }

  .ButtonContainer {
    display: grid;
    gap: 1em;
    justify-content: center;
  }

  .Download {
    margin-right: 0;
  }
}