.Header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: clamp(1.2em, 3.4vw, 2.6em);
  text-align: center;
  gap: 0.8em;
}

.TextSecondary {
  margin-bottom: 3em;
  color: var(--text-secondary);
  font-size: clamp(0.5em, 3.8vw, 0.8em);
  letter-spacing: 0.4rem;
}

h1,
h2,
h3 {
  font-family: Comfortaa, Nunito, cursive;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
}

h2,
h3 {
  font-size: clamp(0.9em, 1.8vw, 1em);
}

h1 {
  font-size: clamp(0.9em, 2vw, 1.1em);
  margin-bottom: 0.4em;
}
